fieldset {
  width: 35rem;
  height: 27rem;
  margin: 10rem auto;
  line-height: 3rem;
}
input {
  height: 2rem;
}
textarea {
  height: 15rem;
}
.form-wrapper {
  display: flex;
  flex-direction: column;
}
label {
 display: flex;
 flex-direction: column;
}
button {
  background-color: gray;
  border: none;
  outline: none;
  padding: .5em .8em;
}
button:hover,
button:active,
button:focus {
  background-color: rgb(88, 87, 87);
  color: #fff;
}